<script setup>
import { useSwitchTab } from "@/hooks/useNavigate"

const orderId = ref("")

const handleClickConfirm = async () => {
  if (!orderId.value) {
    useToast("Please select an match")
    return
  }
  const r = await indexPostMatch({ id: orderId.value })
  if (r.code == 200) {
    useToast(r.msg || 'Matching successful')
    useSwitchTab("/pages/home/index")
  } else {
    useToast(r.msg)
  }
}

onLoad((option) => {
  if (option.orderId) {
    orderId.value = option.orderId
  }
})
</script>

<template>
  <view class="view-wrapper">
    <image
      src="@/static/match/match-image.png"
      style="width: 600rpx; height: 600rpx"
    />
    <view class="bottom-wrapper">
      <view class="bottom-container">
        <view class="confirm-text">
          <p>Match request has been sent!</p>
          <p>We will notify you when it is</p>
          <p>being accepted!</p>
        </view>
        <button class="confirm-button" @click="handleClickConfirm">
          CONFIRM
        </button>
      </view>
    </view>
  </view>
</template>

